/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
a.btn, .btn, button,
input[type=button],
input[type=reset],
input[type=submit] {
  display: inline-flex;
  position: relative;
  white-space: nowrap;
  text-decoration: none;
  line-height: 1;
  border-radius: 1000px;
  font-weight: bold;
  transition: 0.2s;
  border: 0;
}
a.btn:active, .btn:active, button:active,
input[type=button]:active,
input[type=reset]:active,
input[type=submit]:active {
  transition: none;
}
a.btn .icon, .btn .icon, button .icon,
input[type=button] .icon,
input[type=reset] .icon,
input[type=submit] .icon {
  /* icon inherits color of button label */
  color: inherit;
  flex-shrink: 0;
}

/* Main buttons */
a.btn--primary, .btn--primary {
  background-color: #FF2B0F;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a.btn--primary:hover, .btn--primary:hover {
  background-color: #0249B3;
}

a.btn--secondary, .btn--secondary {
  background-color: #FF660D;
  color: #ffffff;
}
a.btn--secondary:active, .btn--secondary:active {
  background-color: var(--btn-secondary-active);
}

a.btn--accent, .btn--accent {
  background-color: #0249B3;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a.btn--accent:hover, .btn--accent:hover {
  background-color: #FF2B0F;
  color: white;
}
a.btn--accent:active, .btn--accent:active {
  background-color: #FF2B0F;
}

/* feedback */
.btn[disabled] {
  cursor: not-allowed;
  background-color: var(--btn-disabled-bg);
  color: var(--btn-disabled-label);
  box-shadow: none;
  opacity: 0.6;
}
.btn[disabled]:visited {
  color: var(--btn-disabled-label);
}

/* button size */
.a.btn--sm, .btn--sm {
  padding: calc(0.5 * 1em) calc(1.5 * 1em);
}

a.btn--md, .btn--md {
  padding: 1em calc(2.5 * 1em);
}

a.btn--lg, .btn--lg {
  padding: 1em calc(2.5 * 1em);
  font-size: 1.25em;
}

.btn--full {
  display: flex;
  width: 100%;
  justify-content: center;
}

/* buttons group */
.btns {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: calc(-1 * var(--space-xs));
}
.btns > * {
  margin-right: var(--space-xs);
  margin-bottom: var(--space-xs);
}
.btns > *:last-of-type {
  margin-right: 0;
}

